.contact-back {
    background-color: var(--background-color);
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    height: 100%;
    width: 80px;
    transition: 0.4s;
}

.contact-back:hover {
    width: 100px;
}

.contact-arrow {
    font-size: 2rem;
    color: var(--spare-color);
    opacity: 0.6;
    transition: 0.4s;
}

.contact-back:hover .contact-arrow {
    opacity: 1;
    transform: scale(1.5);
}

.content-area {
    /* background: var(--base-color3); */
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.icon-box {
    position: relative;
    border-style: solid;
    border-width: 2px;
    border-color: var(--base-color3);
    border-radius: 20%;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.icon-box:hover::before {
    content: "";
    position: absolute;
    top: 10%;
    left: 0;
    width: 0;
    height: 60%;
    align-self: center;
    background-color: var(--base-color3);
    border-radius: 20%;
    -webkit-animation: fill 0.5s forwards;
    animation: fill 0.5s forwards;
    z-index: -1;
}

@-webkit-keyframes fill {
    from {
        width: 0;
    }

    to {
        width: 100%;
        height: 100%;
        top: 0;
    }
}

@keyframes fill {
    from {
        width: 0;
    }

    to {
        width: 100%;
        height: 100%;
        top: 0;
    }
}

.social-icon {
    color: var(--spare-color);
    font-size: 8rem;
    transition: 300ms;
}

.content-area>a {
    text-decoration: none;
}

.icon-box:hover .social-icon {
    font-size: 10rem;
}

.icon-box:active .social-icon {
    font-size: 9rem;
}

#email {
    cursor: pointer;
}

#email-icon {
    transition: 400ms;
}

#email-message {
    position: absolute;
    top: 30%;
    text-align: center;
    font-size: 1.5rem;
    left: 1%;
    color: var(--spare-color);
    opacity: 0;
    transition: 400ms;
}


/* phone support */
@media screen and (max-width: 480px) {
    .content-area {
        flex-direction: column;
        height: 85%;
        align-self: center;
        /* top: 10%; */
    }
}